<template>
  <div class="wrap-home">
    <!--信息部分-->
    <ul class="news-ul">
      <li :key="index" v-for="(item, index) in responseData" class="news-list">
        <a :href="item.url" class="news-title" target="_blank"><h3 v-text="item.desc"></h3></a>
        <div class="news-img">
          <a :href="item.url" v-if="item.type === '福利'" target="_blank"><img :src="item.url + '?imageView2/0/w/300'" alt=""></a>
          <img :key="index" :src="img + '?imageView2/0/w/300'" alt="" v-for="(img, index) in item.images" v-else>
        </div>
        <div class="news-info-wrap"><span class="info-name">类&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;型：</span><span class="news-type" v-text="item.type" :style="{'background-color':listColor[getIndex(item.type)]}" @click="getNews(item.type)"></span></div>
        <div class="news-info-wrap" @click="testBugRecord"><span class="info-name">创建时间：</span><span class="news-createdTime">{{item.publishedAt | dateFormat}}</span></div>
      </li>
    </ul>
    <!--导航部分-->
    <ul class="nav-ul">
      <li><h3>文章分类</h3></li>
      <li><a href='javascript:' @click="getNews('all')">全部</a></li>
      <li><a href='javascript:' @click="getNews('Android')">Android</a></li>
      <li><a href='javascript:' @click="getNews('iOS')">iOS</a></li>
      <li><a href='javascript:' @click="getNews('休息视频')">休息视频</a></li>
      <li><a href='javascript:' @click="getNews('福利')">福利</a></li>
      <li><a href='javascript:' @click="getNews('拓展资源')">拓展资源</a></li>
      <li><a href='javascript:' @click="getNews('前端')">前端</a></li>
      <li><a href='javascript:' @click="getNews('瞎推荐')">瞎推荐</a></li>
      <li><a href='javascript:' @click="getNews('App')">App</a></li>
      <li v-if="scroll !== 0"><a href='javascript:' @click="toTop" class="backToTop">返回顶部</a></li>
    </ul>
  </div>
</template>
<script>
import axios from '../../../bower_components/axios/dist/axios.min'
import Router from 'vue-router'
import Vue from 'vue'

Vue.use(Router)

require('es6-promise').polyfill()

export default {
  filters: {
    dateFormat (value) {
      let date = new Date(value).toString()
      let temIndex = date.indexOf('G')
      date = date.slice(0, temIndex)
      return date
    }
  },
  data () {
    return {
      responseData: {},
      listTypes: ['Android', 'iOS', '休息视频', '福利', '拓展资源', '前端', '瞎推荐', 'App'],
      listColor: ['green', 'pink', 'lightblue', 'orange', 'red', '#e1ac54', 'purple', 'skyblue'],
      curPage: 1,
      curType: 'all',
      scroll: 0
    }
  },
  methods: {
    testBugRecord () {
      const c = []
      console.log(c[1][3])
    },
    toTop () {
      document.body.scrollTop = 0
      document.documentElement.scrollTop = 0
    },
    getClientVal () {   // 1.判断是否是ie9+和最新浏览器
      if (window.innerWidth !== null) {
        return {
          width: window.innerWidth,
          height: window.innerHeight
        }
      } else if (document.compatMode === 'CSS1Compat') {   // 标准浏览器
        return {
          width: document.documentElement.clientWidth,
          height: document.documentElement.clientHeight
        }
      } else {  // 3.怪异模式
        return {
          width: document.body.clientWidth,
          height: document.body.clientHeight
        }
      }
    },
    getOffsetVal () {
      if (window.pageYOffset !== null) { // ie9+  和 最新浏览器
        return {
          left: window.pageXOffset,
          top: window.pageYOffset
        }
      } else if (document.compatMode === 'CSS1Compat') { // 非怪异浏览器/标准浏览器模式
        return {
          left: document.documentElement.scrollLeft,
          top: document.documentElement.scrollTop
        }
      }
      return {
        left: document.body.scrollLeft,
        top: document.body.scrollTop
      }
    },
    scrollListener () {
      clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        let oElem = document.querySelector('.nav-ul')
        if (this.scroll > 150) {
          oElem.style.top = (20 + this.scroll - 150) + 'px'
        } else {
          oElem.style.top = 20 + 'px'
        }
      }, 500)
    },
    getNews (param) {
      this.curPage = 1
      this.curType = param
      axios.get('https://gank.io/api/data/' + this.curType + '/10/1')
        .then((response) => {
          this.responseData = response.data.results
        })
        .catch((error) => {
          console.log(error)
        })
      this.toTop()
    },
    updateNews () {
      clearTimeout(this.updateTimer)
      this.updateTimer = setTimeout(() => {
        let oLastTop = document.querySelectorAll('.news-list')[document.querySelectorAll('.news-list').length - 3].offsetTop
        let windowHeight = this.getClientVal().height
        this.scroll = this.getOffsetVal().top
        if (oLastTop <= this.scroll + windowHeight) {
          this.curPage += 1
          axios.get('https://gank.io/api/data/' + this.curType + '/10/' + this.curPage)
            .then((response) => {
              this.responseData.push.apply(this.responseData, response.data.results)
            })
            .catch((error) => {
              console.log(error)
            })
        }
      }, 100)
    },
    getIndex (ele) {
      return this.listTypes.indexOf(ele)
    }
  },
  created () {
    this.getNews('all')
  },
  mounted () {
    window.addEventListener('scroll', this.scrollListener)
    window.addEventListener('scroll', this.updateNews)
  }
}
</script>
<style scoped>
  .wrap-home {
    width: 100%;
    max-width: 1200px;
    min-width: 800px;
    margin: 0 auto;
    padding-top: 20px;
    overflow: hidden;
    position: relative;
  }
  .news-ul {
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    float: left;
    width: 77%;
    box-sizing: border-box;
  }
  .news-list {
    border-bottom: 1px dotted #ccc;
    margin-bottom: 20px;
    padding: 10px;
  }
  .news-title {
    display: inline-block;
    font-size: 25px;
    font-weight: bold;
    margin-bottom: 20px;
  }
  .news-img {
    margin-bottom: 10px;
  }
  .news-img img {
    width: 300px;
    border-radius: 10px;
  }
  .news-info-wrap {
    margin-bottom: 12px;
  }
  .info-name {
    font-size: 15px;
    font-weight: bold;
    display: inline-block;
    width: 80px;
    text-align: right;
  }
  .news-type {
    display: inline-block;
    border-radius: 20px;
    padding: 6px 15px;
    color: white;
    font-size: 15px;
    cursor: pointer;
  }
  .nav-ul {
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    position: absolute;
    right: 0;
    top: 20px;
    width: 20%;
    box-sizing: border-box;
  }
  .nav-ul li+li {
    margin-bottom: 20px;
    padding-bottom: 5px;
    border-bottom: 1px dotted #ccc;
  }
  .nav-ul li h3 {
    font-size: 18px;
    font-weight: bold;
    line-height: 50px;
  }
  .nav-ul li a {
    display: inline-block;
    width: 100%;
  }
  .backToTop {
    color: #196a4c;
  }
  a:hover {
    color: #196a4c;
  }
</style>



