import Vue from 'vue'
import Router from 'vue-router'
import indexPage from '../components/indexPage/indexPage.vue'
import aboutPage from '../components/aboutPage/aboutPage.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/about',
      name: 'aboutPage',
      component: aboutPage
    },
    {
      path: '/home',
      name: 'indexPage',
      component: indexPage
    }
  ]
})



