<template>
  <div class="wrap">
    <div class="app-head">
      <div class="app-head-inner">
        <router-link :to="{path: '/home'}">
          <img src="../assets/logo.png">
        </router-link>
        <router-link :to="{path: '/about'}" class="about">
          <p>关于本站</p>
        </router-link>
      </div>
    </div>
    <div class="searchBar">
      <input type="text" placeholder="遇事不决问谷哥" v-model="search" @keyup="getData($event)" @keydown.up.prevent="changeUp()" @keydown.down.prevent="changeDown()" @blur="clearSearch()">
      <div class="searchList">
        <ul class="searchList-ul" v-if="searchSuggestions.length !== 0">
          <li :key="index" :class="{selected: index === selectedSuggestion}" v-for="(item, index) in searchSuggestions" v-text="item" @click="select(item)"></li>
        </ul>
      </div>
    </div>
    <div class="container">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>
    <div class="footer inner-c">
      <p>Powered by Frank</p>
      <p>Data from <a href="http://gank.io/">干货集中营</a></p>
    </div>
  </div>
</template>
<style>
  .wrap {
    width: 100%;
  }
  .inner-c {
    width: 100%;
    max-width: 1200px;
    min-width: 800px;
  }
  a {
    color: inherit;
    text-decoration: none;
  }
  body {
    background: #e5e5e5;
    font-size: 14px;
    color: #444;
  }
  .app-head {
    background: #363636;
    color: #b2b2b2;
    width: 100%;
    padding: 20px;
    box-sizing: border-box;
  }
  .app-head-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1200px;
    min-width: 800px;
    margin: 0 auto;
  }
  .app-head-inner img {
    width: 50px;

  }
  .searchBar {
    background: #363636;
    padding: 10px 20px;
    width: 100%;
    box-sizing: border-box;
  }
  .searchBar input {
    display: block;
    border-radius: 10px;
    height: 30px;
    width: 100%;
    max-width: 1200px;
    min-width: 800px;
    margin: 0 auto;
    outline: none;
    padding: 0 20px;
    box-sizing: border-box;
  }
  .searchList {
    width: 100%;
    max-width: 1200px;
    min-width: 800px;
    margin: 0 auto;
    background-color: #fff;
    position: relative;
  }
  .searchList-ul {
    border-radius: 10px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 999;
    background-color: #fff;
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
  }
  .searchList-ul li {
    border-radius: 5px;
    padding: 10px;
    cursor: default;
  }
  .selected {
    background-color: #ccc;
  }
  .searchList-ul li:hover {
    background-color: #ccc;
  }
  .about {
    float: right;
  }
  .about span {
    font-size: 15px;
    font-weight: bold;
  }
  .container {
    margin-bottom: 20px;
    padding: 0 20px;
  }
  .footer {
    width: 100%;
    border-top: 1px dashed grey;
    margin: 0 auto;
    padding: 30px;
    text-align: center;
    box-sizing: border-box;
  }
  .footer p {
    margin-bottom: 10px;
  }
  .footer p a {
    text-decoration: underline;
  }
</style>
<script>
export default {
  data () {
    return {
      search: '',
      searchSuggestions: [],
      searchTimer: {},
      clearTimer: {},
      selectedSuggestion: -1
    }
  },
  methods: {
    httpRequest (url) {
      let body = document.querySelector('body')
      // 插入一个script
      let script = document.createElement('script')
      window.baidu = {}
      window.baidu.sug = (res) => {
        this.searchSuggestions = res.s
        delete window['baidu']
      }
      url += this.search
      script.setAttribute('src', url)
      body.appendChild(script)
    },
    getData (ev = {}) {
      const HTTPURL = 'https://sp0.baidu.com/5a1Fazu8AA54nxGko9WTAnF6hhy/su?wd='

      // 屏蔽上下键的功能
      if (ev.keyCode === 38 || ev.keyCode === 40) { return }

      // 回车键进入搜索
      if (ev.keyCode === 13) {
        window.open('https://d.ggkai.men/search?q=' + this.search)
        this.search = ''
        this.searchSuggestions = []
        this.selectedSuggestion = -1
        return
      }
      // 调用
      clearTimeout(this.searchTimer)
      this.searchTimer = setTimeout(() => {
        this.httpRequest(HTTPURL)
        // 初始化选择
        this.selectedSuggestion = -1
      }, 500)
    },
    select (item) {
      window.open('https://d.ggkai.men/search?q=' + item)
      this.search = ''
      this.searchSuggestions = []
    },
    changeUp () {
      if (this.selectedSuggestion <= 0) {
        return
      }
      this.selectedSuggestion -= 1
      this.search = this.searchSuggestions[this.selectedSuggestion]
      this.getData()
    },
    changeDown () {
      if (this.selectedSuggestion >= this.searchSuggestions.length - 1) {
        return
      }
      this.selectedSuggestion += 1
      this.search = this.searchSuggestions[this.selectedSuggestion]
      this.getData()
    },
    clearSearch () {
      clearTimeout(this.clearTimer)
      this.clearTimer = setTimeout(() => {
        this.search = ''
        this.searchSuggestions = []
        this.selectedSuggestion = -1
      }, 100)
    }
      /* axios({
        method: 'get',
        url: 'https://d.ggkai.men/complete/search',
        withCredentials: true,
        params: {
          client: 'psy-ab',
          hl: 'en-HK',
          gs_rn: 64,
          gs_ri: 'psy-ab',
          cp: 1,
          gs_id: '2n',
          q: this.search
        }
        // https://d.ggkai.men/complete/search?client=psy-ab&hl=en-HK&gs_rn=64&gs_ri=psy-ab&cp=1&gs_id=2n&q=a
      })
        .then((response) => {
          console.log(response)
        })
        .catch((error) => {
          console.log(error)
        }) */
  }
}
</script>



